import { css } from '@emotion/react';
import { useEffect, useRef, useState } from 'react';

import { FullHeightBox } from 'src/components/layout';

interface PaperProps {
  children: React.ReactNode;
  backgroundUrl?: string;
}

const scrollableItem = css`
  position: relative;
  scroll-snap-align: start;
`;

const background = (url: PaperProps['backgroundUrl']) => css`
  ${url
    ? ` background-image:  url(${url});
    background-repeat: no-repeat;
    background-size: auto 100% ; 
    background-position: center;`
    : `background: #ffffff;`}
`;

const Paper = ({
  children,
  backgroundUrl,
  ...others
}: PaperProps): JSX.Element => {
  const [isIntersecting, setIsIntersecting] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(([entry]) => {
      console.log('IntersectionObserver callback', entry);
      setIsIntersecting(entry.isIntersecting);
    });
    console.log('IntersectionObserver - observe', ref.current);
    console.log('IntersectionObserver - observe', backgroundUrl);
    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      console.log('IntersectionObserver - disconnect', ref.current);
      console.log('IntersectionObserver - disconnect', backgroundUrl);
      observer.disconnect();
    };
  }, [isIntersecting, backgroundUrl]);

  useEffect(() => {
    if (isIntersecting) {
      console.log('Paper - isIntersecting', isIntersecting);
      if (ref.current) {
        console.log('Paper - isIntersecting', backgroundUrl);
        setTimeout(() => {
          if (!ref?.current) {
            return;
          }
          const divElement = ref.current as HTMLDivElement;
          const rect = divElement.getBoundingClientRect();
          if (!rect) {
            return;
          }
          const { top = 0 } = rect ?? {};
          if (top === 0) {
            return;
          }
          divElement.scrollIntoView({
            block: 'start',
            behavior: 'smooth',
          });
        }, 1000);
      }
    }
  }, [isIntersecting, ref, backgroundUrl]);
  // const onScrollEnd = useCallback(() => {
  //   console.log('onScrollEnd');
  // }, []);
  // const onScroll = useCallback(() => {
  //   console.log('onScroll');
  // }, []);
  // useEffect(() => {
  //   if(ref.current) {
  //     console.log(ref.current.getClientRects().top);
  //   }
  //   // console.log('Paper useEffect', ref.current);
  //   // window.addEventListener('scrollend', onScrollEnd);
  //   // window.addEventListener('scroll', onScroll);
  //   //
  //   // return () => {
  //   //   window.removeEventListener('scrollend', onScrollEnd);
  //   //   window.removeEventListener('scroll', onScroll);
  //   // };
  // }, [ref]);

  return (
    <FullHeightBox
      css={[scrollableItem, background(backgroundUrl)]}
      {...others}
      ref={ref}
    >
      {children}
    </FullHeightBox>
  );
};

export default Paper;
